.HorizontalItem {
    flex-shrink: 0;
    width: 80px;
    height: 114px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 12px;
}

.HorizontalItem:first-child {
    padding-left: 4px;
}

.HorizontalItem:last-child {
    padding-right: 4px;
}

.ResultCard {
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ResultCard h1 {
    text-align: center;
    color: #fff;
    text-shadow: 4px 4px 5px #000, -4px 4px 5px #000, -4px -4px 5px #000, 4px -4px 5px #000
}